/* eslint-disable jsx-a11y/click-events-have-key-events */
import React, { FC } from 'react'
import clsx from 'clsx'
import { MenuProps, MenuItemProps } from './types'

export const MenuItem: FC<MenuItemProps> = ({
  onClick,
  onClose,
  className,
  children,
}) => {
  return (
    <li
      aria-selected='true'
      className={clsx('DropdownMenu__Item', className)}
      onClick={(e): void => {
        if (onClose) {
          onClose(e)
        }
        if (onClick) {
          onClick(e)
        }
      }}
      role='option'
      tabIndex={-1}
    >
      {children}
    </li>
  )
}

const Menu: FC<MenuProps> = ({ children, className, direction = 'bl' }) => {
  return (
    <ul
      className={clsx('DropdownMenu', className, `--${direction}`)}
      role='listbox'
    >
      {children}
    </ul>
  )
}

export default Menu
